
.enter {
  font-family: Helvetica-Regular;
  display: flex;
  flex-direction: column;
  margin: 167px auto;
  width: 376.5px;

  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 16.5px;

    &__text {
      margin-left: 10px;
      font-style: normal;
      font-weight: normal;
      font-size: 24px;
      line-height: 28px;
      color: #3D5170;
    }
  }

  &__form {
    width: 376.5px;
    height: 253.5px;
    display: flex;
    flex-direction: column;
    background: #FFFFFF;
    box-shadow: 0px 1px 0px rgba(90, 97, 105, 0.11), 0px 2px 4px rgba(90, 97, 105, 0.12), 0px 5px 5px rgba(90, 97, 105, 0.06), 0px 3.5px 35px rgba(90, 97, 105, 0.1);
    border-radius: 9px;

  }
  &__title {
    text-align: center;
    margin: 19px 0 30px;
    font-style: normal;
    font-weight: normal;
    font-size: 17.5px;
    line-height: 20px;
    color: #3D5170;
  }

  &__label {
    flex-direction: column;
    display: flex;
    font-style: normal;
    font-weight: normal;
    font-size: 10.5px;
    line-height: 12px;
    color: #495057;

  }
  &__auth{
    margin: 0 18px;
    font-style: normal;
    font-weight: normal;
    font-size: 10.5px;
    line-height: 12px;
    color: #495057;

  }
  &__footer{
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin: 16px 18px;

      .footer__button{
        width: 110px;
        height: 29px;
        font-style: normal;
        font-weight: normal;
        font-size: 11.5px;
        line-height: 13px;
        align-items: center;
        text-align: center;
        letter-spacing: -0.0821429px;
        color: #FFFFFF;
        background: #007BFF;
        border: 0.5px solid #007BFF;
        border-radius: 4px;
      }
    .footer__access{
      font-style: normal;
      font-weight: normal;
      font-size: 10.5px;
      line-height: 12px;
      color: #007BFF;
    }

  }
}
